@import 'vars';
@import 'reset';

body {
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app-layout {
  display: flex;
  flex-direction: column;
  height: 95vh;
  font-family: var(--appGeneralFont);
  background: var(--appBg);

  &__header {
    flex-shrink: 0;
  }

  &__main {
    flex-grow: 1;
    padding-bottom: 32px;
    overflow-y: auto;
  }

  &__footer {
    padding: 0 0 40px;
    text-align: center;
  }
}

.app-title {
  color: var(--appTitleColor);
  font-weight: 700;
  word-break: break-all;

  &--h1 {
    font-size: 32px;
    line-height: 44px;
  }

  &--h2 {
    font-size: 22px;
    line-height: 30px;
  }

  &--h3 {
    font-size: 18px;
    line-height: 22px;
  }

  &--h4 {
    font-size: 16px;
    line-height: 20px;
  }
}

.app-text {
  color: var(--appTextColor);
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  word-break: break-word;

  &--link {
    color: var(--appTextLinkColor);
    font-weight: 700;
    text-decoration: none;
    &:active {
      transform: scale(0.9);
    }
  }

  &--desc {
    color: var(--appTextDescColor);
  }

  &--divider {
    margin-top: 8px;
  }

  strong {
    font-weight: 700;
  }
}

.app-tabs {
  display: grid;
  grid-auto-flow: column;
  justify-content: flex-start;
  grid-column-gap: 8px;
  padding: 8px 16px;
  white-space: nowrap;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
  &::after {
    content: '';
    display: block;
    width: 8px;
  }
  &::-webkit-scrollbar {
    display: none;
  }
}

.app-card-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 8px;
  padding: 8px 16px 32px;
}

.app-definition {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-column-gap: 16px;
  grid-row-gap: 8px;
  margin-top: 8px;
}

.app-form {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 16px;
  margin-top: 24px;
  padding: 0 24px;

  &__divider {
    margin-top: 16px;
    margin-bottom: 8px;
  }

  &__divider-inner {
    margin-top: 16px;
  }

  &__group {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 8px;

    &--files {
      grid-row-gap: 16px;
    }
  }
}

.app-file {
  justify-self: baseline;
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0 32px;
  background: url("data:image/svg+xml,%3Csvg width='15' height='19' viewBox='0 0 15 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.7995 3.59101L11.3905 0.198124C11.327 0.134786 11.2514 0.0847286 11.1682 0.0508928C11.0859 0.0174546 10.9979 0.00015904 10.9091 0H2.04545C0.91578 0 0 0.911419 0 2.03571V16.9643C0 18.0886 0.91578 19 2.04545 19H12.9545C14.0842 19 15 18.0886 15 16.9643V4.07143C14.999 3.89131 14.9271 3.71879 14.7995 3.59101ZM11.5909 2.31666L12.6723 3.39288H11.5909V2.31666ZM13.6363 16.9643C13.6363 17.3391 13.3311 17.6429 12.9545 17.6429H2.04545C1.66888 17.6429 1.36362 17.3391 1.36362 16.9643V2.03571C1.36362 1.66093 1.66888 1.35713 2.04545 1.35713H10.2273V4.07143C10.2273 4.4462 10.5325 4.75001 10.9091 4.75001H13.6364V16.9643H13.6363Z' fill='%23111111' fill-opacity='0.3' /%3E%3C/svg%3E")
    0 0 no-repeat;

  &__title {
    max-width: 196px;
    color: var(--appFileTitleColor);
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  &__size {
    color: var(--appFileSizeColor);
    font-size: 12px;
    line-height: 16px;
  }
  &__close {
    position: absolute;
    top: 1px;
    right: 0;
    color: var(--appFileCloseColor);
    &:active {
      transform: scale(0.9);
    }
  }
}
