.service-desk {
  margin: 8px 0 32px;
  &__header {
    margin: 0 24px 16px;
  }
  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 9px;
    margin: 0 16px;
  }
}

$serviceDeskCardBg: var(--serviceDeskCardBg);
$serviceDeskCardBorderColor: var(--serviceDeskCardBorderColor);
.service-desk-card {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  padding: 16px;
  background: $serviceDeskCardBg;
  border: 1px solid $serviceDeskCardBorderColor;
  border-radius: 4px;
  &:active {
    transform: scale(0.95);
  }

  &__title {
    flex-grow: 1;
    align-self: flex-start;
    padding-right: 20%;
    line-height: 20px;
  }

  &__pic {
    align-self: flex-end;
    margin-top: 27px;
    width: auto;
    height: 104px;
  }
}
