.notifications {
  margin: 0 16px;

  &__header {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin: 0 0 16px 8px;
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 8px;
  }
}

.notifications-empty {
  display: flex;
  flex-direction: column;
  padding: 56px 0 0;
  align-items: center;
  justify-content: center;

  &__icon {
    margin-top: 18px;
    width: 50px;
    height: 50px;
  }
}
