$formFieldBg: var(--formFieldBg);
$formFieldBorderColor: var(--formFieldBorderColor);
$formFieldBorderColorFocus: var(--formFieldBorderColorFocus);
$formFieldBorderColorError: var(--formFieldBorderColorError);
$formFieldColor: var(--formFieldColor);
$formFieldColorPlaceholder: var(--formFieldColorPlaceholder);
$formFieldColorPlaceholderSelected: var(--formFieldColorPlaceholderSelected);
$formFieldDescriptionColor: var(--formFieldDescriptionColor);

.form-field {
  display: flex;
  flex-direction: column;

  &__field {
    position: relative;
    padding: 12px 16px;
    font: var(--appGeneralFont);
    color: $formFieldColor;
    font-size: 16px;
    line-height: 22px;
    border: 1px solid $formFieldBorderColor;
    border-radius: 3px;
    outline: none;
    appearance: none;
    resize: none;
    overflow: hidden;
    &::placeholder {
      color: $formFieldColorPlaceholder;
    }
    &:focus {
      border-color: $formFieldBorderColorFocus;
    }

    &--error {
      border-color: $formFieldBorderColorError;
    }

    &--dropdown {
      padding: 0;
      overflow: visible;

      .Dropdown-control {
        padding: 12px 16px;
      }

      .Dropdown-placeholder {
        color: $formFieldColorPlaceholder;

        &.is-selected {
          color: $formFieldColor;
        }
      }

      .Dropdown-arrow {
        position: absolute;
        top: 21px;
        right: 16px;
        width: 8px;
        height: 5px;
        background: $formFieldBg
          url("data:image/svg+xml,%3Csvg width='8' height='5' viewBox='0 0 8 5' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.330314 1.86173L3.21591 4.68155C3.65042 5.10615 4.35232 5.10615 4.78684 4.68155L7.67243 1.86173C8.37434 1.17583 7.87298 0 6.8814 0H1.11021C0.118629 0 -0.371588 1.17583 0.330314 1.86173Z' fill='%23111111' fill-opacity='0.3'/%3E%3C/svg%3E")
          50% 50% no-repeat;
      }

      .Dropdown-menu {
        position: absolute;
        z-index: 1;
        top: 100%;
        left: -1px;
        right: -1px;
        padding: 8px 0;
        background: $formFieldBg;
        border: 1px solid $formFieldBorderColor;
        box-sizing: border-box;
        border-radius: 4px;
      }

      .Dropdown-option {
        padding: 8px 16px;
        font-size: 16px;
        line-height: 22px;
        cursor: pointer;

        &:hover {
          color: var(--formFieldColorPlaceholderSelected);
        }
        &.is-selected {
          color: $formFieldColorPlaceholderSelected;
        }
      }
    }
  }

  &__description {
    margin-top: 4px;
    color: $formFieldDescriptionColor;
    font-size: 12px;
    line-height: 16px;

    ul {
      padding-right: 50px;
      li {
        position: relative;
        padding-left: 10px;
        &::before {
          position: absolute;
          left: 0;
          content: 'â€¢';
        }
      }
    }
  }
}
