$alertBackfaceBg: var(--alertBackfaceBg);
$alertBg: var(--alertBg);
$alertBorderColor: var(--alertBorderColor);

.alert {
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding: 24px;

  &::before {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    background: $alertBackfaceBg;
  }

  &__box {
    position: relative;
    padding: 20px 24px;
    background: $alertBg;
    border: 1px solid $alertBorderColor;
    border-radius: 4px;
  }

  &__footer {
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    grid-column-gap: 16px;
    white-space: nowrap;
    margin-top: 16px;
  }
}
