$color-darkGrey100: #111111;
$color-darkGrey70: rgba(17, 17, 17, 0.7);
$color-darkGrey50: rgba(17, 17, 17, 0.5);
$color-darkGrey30: rgba(17, 17, 17, 0.3);
$color-darkGrey5: rgba(17, 17, 17, 0.05);
$color-white: #fff;
$color-blue: #4799e3;
$color-red: #ed4747;
$color-green: #00b012;
$color-lightGrey: #f9f9f9;

:root {
  --appGeneralFont: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;

  --appBg: #{$color-white};
  --appTitleColor: #{$color-darkGrey100};
  --appTextColor: #{$color-darkGrey100};
  --appTextLinkColor: #{$color-blue};
  --appTextDescColor: #{$color-darkGrey50};

  --headerNavBtnColor: #{$color-blue};

  --serviceDeskCardBg: rgba(249, 249, 249, 0.5);
  --serviceDeskCardBorderColor: rgba(17, 17, 17, 0.025);

  --cardBg: #{$color-lightGrey};
  --cardBorderColor: #{$color-darkGrey5};
  --cardTimeColor: #{$color-darkGrey50};

  --notifierBg: #{$color-darkGrey70};
  --notifierBorderColor: #{$color-darkGrey30};
  --notifierColor: #{$color-white};

  --alertBackfaceBg: rgba(0, 0, 0, 0.2);
  --alertBg: #{$color-white};
  --alertBorderColor: #{$color-darkGrey5};

  --buttonPrimaryColor: #{$color-white};
  --buttonPrimaryBg: #{$color-blue};
  --buttonAlertColor: #{$color-white};
  --buttonAlertBg: #{$color-red};
  --buttonTabColor: #{$color-darkGrey30};
  --buttonTabBg: #{$color-darkGrey5};

  --formFieldBg: #{$color-white};
  --formFieldBorderColor: #{$color-darkGrey5};
  --formFieldBorderColorFocus: #{$color-blue};
  --formFieldBorderColorError: #{$color-red};
  --formFieldColor: #{$color-darkGrey100};
  --formFieldColorPlaceholder: #{$color-darkGrey30};
  --formFieldColorPlaceholderSelected: #{$color-blue};
  --formFieldDescriptionColor: rgba(0, 0, 0, 0.5);

  --appFileTitleColor: #{$color-darkGrey100};
  --appFileSizeColor: #{$color-darkGrey30};
  --appFileCloseColor: #{$color-blue};
}
