$buttonPrimaryColor: var(--buttonPrimaryColor);
$buttonPrimaryBg: var(--buttonPrimaryBg);
$buttonAlertColor: var(--buttonAlertColor);
$buttonAlertBg: var(--buttonAlertBg);
$buttonTabColor: var(--buttonTabColor);
$buttonTabBg: var(--buttonTabBg);

input[type="file"] {
  display: none;
}

.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px 32px;
  background: transparent;
  border: none;
  border-radius: 3px;
  outline: none;
  font-family: inherit;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.4;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  text-transform: uppercase;
  -webkit-user-select: none;
  user-select: none;
  justify-self: baseline;
  &:active {
    transform: scale(0.9);
  }

  &--primary {
    $buttonPrimary: & !global;
    color: $buttonPrimaryColor;
    background: $buttonPrimaryBg;
  }

  &--alert {
    color: $buttonAlertColor;
    background: $buttonAlertBg;
  }

  &--tab {
    color: $buttonTabColor;
    background: $buttonTabBg;
  }

  &--outline {
    padding: 7px 31px;
    &#{$buttonPrimary} {
      color: $buttonPrimaryBg;
      background: transparent;
      border: 1px solid $buttonPrimaryBg;
    }
  }
}
