$notifierBg: var(--notifierBg);
$notifierBorderColor: var(--notifierBorderColor);
$notifierColor: var(--notifierColor);

.notifier {
  position: fixed;
  left: 8px;
  right: 8px;
  bottom: 60px;
  padding: 16px;
  color: $notifierColor;
  background: $notifierBg;
  border: 1px solid $notifierBorderColor;
  border-radius: 4px;
  color: #fff;
  font-family: var(--appGeneralFont);

  strong {
    font-weight: 700;
  }
}
