$cardBg: var(--cardBg);
$cardBorderColor: var(--cardBorderColor);
$cardTimeColor: var(--cardTimeColor);

.card {
  display: block;
  padding: 16px;
  text-decoration: none;
  background: $cardBg;
  border: 1px solid $cardBorderColor;
  border-radius: 4px;
  &--link {
    &:active {
      transform: scale(0.95);
    }
  }
  &--closed {
    pointer-events: none;
    & > * {
      opacity: 0.5;
    }
  }

  strong {
    font-weight: 700;
  }

  &__header {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-height: 20px;
    overflow: hidden;
  }

  &__time {
    display: block;
    margin-top: 8px;
    color: $cardTimeColor;
    font-size: 12px;
    font-weight: 400;
  }

  &__info {
  }
}
