.header-nav {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;

  &__title {
    position: absolute;
    top: 50%;
    left: 50%;
    width: calc(100% - 120px);
    transform: translate(-50%, -50%);
    text-align: center;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.2;
  }
}

$headerNavBtnColor: var(--headerNavBtnColor);
.header-nav-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1px 0;
  border: 0;
  background: none;
  color: $headerNavBtnColor;
  outline: none;
  text-decoration: none;
  cursor: pointer;
  z-index: 1;
  &:active {
    transform: scale(0.9);
  }

  &--icon {
    width: 32px;
    height: 32px;
  }

  &__icon {
    pointer-events: none;
  }

  &__text {
    font-size: 18px;
    font-weight: 700;
  }
  &__disabled {
    cursor: default;
    color: var(--appTextDescColor);
  }
}
